<template>
  <div>
    <div class="container">

    <div class="login">
      <div class="head"></div>
      <div class="tou">
        <img class="img" src="../../../public/static/logo/police.png" />
      </div>

      
     <div :class="['con','none']">
        <div class="con_01">
          <div class="con_01_l">
            <img
              class="usericon"
              src="../../../public/static/logo/logo_2.png"
              mode=""
            />
          </div>

          <div class="con_01_r">
            <input
              v-model="username"
              class="uni-input"
              type="tel"
              placeholder="请输入账号"
              style="background: none"
            />
          </div>
        </div>


        <div class="con_02">
          <div class="con_02_l">
            <img
              class="usericon"
              src="../../../public/static/logo/logo_1.png"
              mode=""
            />
          </div>
          <div class="con_02_r">
            <input
              v-model="password"
              class="uni-input"
              type="password"
              @confirm="login()"
              placeholder="请输入密码"
              style=" background: none"
            />
          </div>
         
        </div>


        <div class="con_04" style="margin-top: 5rem">
          <div>


            <div class="con_04_1" style="font-size: 18px" @click="login()">
              登录
            </div>
          </div>
