<script>

import { login } from "@/api/house";
  export default {
    data() {
      return {
        username: "",
        password: ""
      }
    },
    methods: {
     //登录
    login() {
      if(this.username==""){
      	this.$api.msg('请填写帐号')
      }else if (this.password == "") {
        this.$api.msg("请填写密码");
      }else {
        this.$store.dispatch("user/login",{
            username: this.username,
            password: this.password,
          }).then((res) => {
            this.$api.msg("登录成功");
            this.$router.push({
              path: "/",
              query: {
                data: res,
              },
            });
          }).catch((e)=>{});
      } 
      // login() {
      //   this.$store.dispatch('user/login',{
      //       username:this.username,
      //       password:this.password
      //   }).then(res =>{
      //     this.$router.push({
      //       path:"/"
      //     })
      //   })
      }

      
    },
  }
</script>

